:root {
  --base03: #002b36;
  --base02: #073642;
  --base01: #586e75;
  --base00: #657b83;
  --base0:  #839496;
  --base1:  #93a1a1;
  --base2:  #eee8d5;
  --base3:  #fdf6e3;

  --yellow:  #b58900;
  --orange:  #cb4b16;
  --red:     #dc322f;
  --magenta: #d33682;
  --violet:  #6c71c4;
  --blue:    #268bd2;
  --cyan:    #2aa198;
  --green:   #859900;
}

h1, h2, h3, h4, h5, h6 {
  color: var(--base01);
  font-weight: 600;
  margin-top: 1.5rem;
  margin-bottom: 0.5rem;
}

p {
  margin-bottom: 1rem;
}

a {
  color: var(--blue);
  text-decoration: none;
}


a:hover {
  text-decoration: none;
}

.container {
  background-color: var(--base3);
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  margin:0;
}

.blog-post-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1.5rem;
}

.post-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
}

/* Responsive */
@media (max-width: 1024px) {
  .post-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 640px) {
  .post-grid {
    grid-template-columns: 1fr;
  }
}

.card {
  background-color: var(--base2);
  border: 1px solid var(--base1);
  border-radius: 8px;
  padding: 1.25rem;
  transition: transform 0.15s ease, box-shadow 0.15s ease;
}

.card-title {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  color: var(--base00);
}

.card-title:hover {
  color: var(--cyan);
  cursor: pointer;
  font-weight: 1200;
}


.card-date {
  font-size: 0.875rem;
  color: var(--yellow);
}

.card:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.08);
}

.button {
  background-color: var(--blue);
  color: var(--base3);
  border: none;
  border-radius: 6px;
  padding: 0.5rem 1rem;
  cursor: pointer;
  font-weight: 500;
}

.button:hover {
  background-color: var(--cyan);
}

pre, code {
  font-family: "JetBrains Mono", "Fira Code", monospace;
  background-color: var(--base02);
  color: var(--base2);
}

pre {
  padding: 1rem;
  border-radius: 6px;
  overflow-x: auto;
}

code {
  padding: 0.15rem 0.3rem;
  border-radius: 4px;
}

input, textarea, select {
  background-color: var(--base3);
  color: var(--base00);
  border: 1px solid var(--base1);
  border-radius: 6px;
  padding: 0.5rem;
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
  border-color: var(--blue);
}

.text-success { color: var(--green); }
.text-warning { color: var(--orange); }
.text-error   { color: var(--red); }

/* Blog Post Content Styles */

.post-content {
  text-align: justify;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  color: var(--base00);
  font-size: 1.25rem;
  line-height: 1.75;
}

/* Headings */
.post-content h1,
.post-content h2,
.post-content h3,
.post-content h4,
.post-content h5,
.post-content h6 {
  color: var(--base01);
  font-weight: 800;
  line-height: 1.3;
  margin: 2.5rem 0 1rem;
}

.post-content h1 { font-size: 2.25rem; }
.post-content h2 { font-size: 1.875rem; }
.post-content h3 { font-size: 1.75rem; }
.post-content h4 { font-size: 1.5rem; }
.post-content h5 { font-size: 1.25rem; }
.post-content h6 { font-size: 1rem; }

/* Paragraphs */
.post-content p {
  margin: 1.25rem 0;
}

/* Links */
.post-content a {
  color: var(--blue);
  text-decoration: underline;
  text-underline-offset: 2px;
}

.post-content a:hover {
  color: var(--cyan);
}

/* Lists */
.post-content ul,
.post-content ol {
  margin: 1.25rem 0 1.25rem 1.5rem;
  padding: 0;
}

.post-content li {
  margin: 0.5rem 0;
}

.post-content ul li::marker {
  color: var(--cyan);
}

.post-content ol li::marker {
  color: var(--violet);
}

/* Blockquotes */
.post-content blockquote {
  margin: 2rem 0;
  padding: 1rem 1.25rem;
  border-left: 4px solid var(--cyan);
  background-color: var(--base2);
  color: var(--base01);
  font-style: italic;
}

/* Inline code */
.post-content code {
  background-color: var(--base2);
  color: var(--magenta);
  padding: 0.2rem 0.4rem;
  border-radius: 4px;
  font-size: 0.95em;
}

/* Code blocks */
.post-content pre {
  background-color: var(--base02);
  color: var(--base2);
  padding: 1.25rem;
  border-radius: 8px;
  overflow-x: auto;
  margin: 2rem 0;
}

.post-content pre code {
  background: none;
  padding: 0;
  color: inherit;
  font-size: 0.9rem;
}

/* Tables */
.post-content table {
  width: 100%;
  border-collapse: collapse;
  margin: 2rem 0;
}

.post-content th,
.post-content td {
  border: 1px solid var(--base1);
  padding: 0.75rem 1rem;
}

.post-content th {
  background-color: var(--base2);
  color: var(--base01);
  text-align: left;
}

/* Horizontal rule */
.post-content hr {
  border: none;
  border-top: 1px solid var(--base1);
  margin: 3rem 0;
}

/* Images */
.post-content img {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
  margin: 2rem 0;
}

/* Emphasis */
.post-content strong {
  color: var(--base01);
  font-weight: 600;
}

.post-content em {
  font-style: italic;
}

/* Footnotes / small text */
.post-content small {
  color: var(--base1);
}

.scroll-to-top {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  border: none;

  background-color: var(--base2);
  color: var(--blue);

  font-size: 1.25rem;
  font-weight: bold;

  cursor: pointer;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.12);

  opacity: 0;
  pointer-events: none;
  transform: translateY(10px);
  transition: opacity 0.25s ease, transform 0.25s ease;
}

.scroll-to-top.show {
  opacity: 1;
  pointer-events: auto;
  transform: translateY(0);
}

.scroll-to-top:hover {
  background-color: var(--cyan);
  color: var(--base3);
}

.post-layout {
  display: grid;
  grid-template-columns: minmax(0, 1fr) 260px;
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
  align-items: start; /* ✅ important */
}

.toc {
  position: sticky;
  top: 20px; /* adjust to header height */
  align-self: start; /* ✅ required for grid */
  max-height: calc(100vh - 120px);
  overflow-y: auto;
}


.toc-title {
  font-weight: bold;
  margin-bottom: 1rem;
}

.toc ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.toc li {
  margin-bottom: 1rem;
}

.toc-sub {
  margin-left: 1rem;
  font-size: 0.85rem;
}

.toc a {
  text-decoration: none;
  color: #586e75;
}

.toc a:hover {
  color: #268bd2;
}

/* Layout */
.app-layout {
  display: grid;
  grid-template-columns: 220px 1fr;
  min-height: 100vh;
}

/* Left Navbar */
.left-nav {
  position: sticky;
  top: 0;
  height: 100vh;
  background-color: #fdf6e3;
  border-right: 1px solid #eee8d5;
  padding: 1.5rem 1rem;
  box-sizing: border-box;
}

/* Title */
.nav-title {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 1.5rem;
  color: #586e75;
}

/* Links */
.left-nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.left-nav li {
  margin-bottom: 1.5rem;
}

.left-nav a {
  text-decoration: none;
  color: #586e75;
  font-size: 1.5rem;
}

.left-nav a:hover {
  color: #268bd2;
}

/* Main content */
.main-content {
  padding: 2rem;
}
